*,
::after,
::before {
  margin: 0;
  padding: 0;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
}

html {
  font-family: "Nunito", sans-serif;
  line-height: 1.42;
  font-size: 16px;
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
  height: 100%;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

body {
  height: 100%;
  color: #303030;
  font-family: "Nunito", sans-serif;
  font-weight: 400;
  font-size: 16px;
  font-size: 1rem;
  line-height: 1.4;
  background: #fff;
  scroll-behavior: smooth;
}
@media (max-width: 1280px) {
  body {
    font-size: 18px;
    font-size: 1.125rem;
  }
}
@media (max-width: 599px) {
  body {
    font-size: 16px;
    font-size: 1rem;
  }
}

input::-ms-clear {
  display: none;
}

/* Для Safari */
input[type=search]::-webkit-search-cancel-button {
  -webkit-appearance: none;
}

p {
  margin: 0px 0px 20px;
}
@media (max-width: 1140px) {
  p {
    margin: 0 0 18px;
  }
}
@media (max-width: 480px) {
  p {
    margin: 0 0 16px;
  }
}

ul, ol {
  padding: 0px;
  margin: 0px;
  list-style: none;
}

li {
  list-style: none;
}

h1, h2, h3, h4, h5, h6,
.h1, .h2, .h3, .h4, .h5, .h6, .h20 {
  font-family: "Nunito";
  font-weight: 500;
  color: #303030;
  margin: 40px 0px 40px;
  text-transform: none;
  line-height: 1.2;
}
