    font-size: 3.125rem;
  }
}
@media (max-width: 959px) {
  h1, .h1 {
    font-size: 46px;
    font-size: 2.875rem;
  }
}
@media (max-width: 599px) {
  h1, .h1 {
    font-size: 36px;
    font-size: 2.25rem;
    line-height: 1.14;
  }
}
@media (max-width: 480px) {
  h1, .h1 {
    font-size: 28px;
    font-size: 1.75rem;
  }
}

h2, .h2, .ui-h2 {
  font-size: 48px;
  font-size: 3rem;
  line-height: 1.2;
}
@media (max-width: 1280px) {
