    font-size: 42px;
    font-size: 2.625rem;
  }
}
@media (max-width: 959px) {
  h2, .h2, .ui-h2 {
    font-size: 40px;
    font-size: 2.5rem;
  }
}
@media (max-width: 599px) {
  h2, .h2, .ui-h2 {
    font-size: 32px;
    font-size: 2rem;
  }
}
@media (max-width: 480px) {
  h2, .h2, .ui-h2 {
    font-size: 24px;
    font-size: 1.5rem;
  }
}

h3, .h3, .ui-h3 {
  font-size: 40px;
  font-size: 2.5rem;
  line-height: 1.2;
}
@media (max-width: 959px) {
  h3, .h3, .ui-h3 {
    font-size: 32px;
    font-size: 2rem;
  }
}
@media (max-width: 599px) {
  h3, .h3, .ui-h3 {
    font-size: 26px;
    font-size: 1.625rem;
  }
}
@media (max-width: 480px) {
  h3, .h3, .ui-h3 {
    font-size: 24px;
    font-size: 1.5rem;
  }
}

h4, .h4, .ui-h4 {
  font-size: 30px;
  font-size: 1.875rem;
}

h5, .h5 {
  font-size: 24px;
  font-size: 1.5rem;
}

h6, .h6, .ui-h5 {
  font-size: 16px;
  font-size: 1rem;
  text-transform: none;
  font-weight: 700;
}

.ui-font-midi {
  font-size: 16px;
  font-size: 1rem;
}

.ui-font-mini {
  font-size: 14px;
  font-size: 0.875rem;
}

.h20 {
  font-size: 20px;
  font-size: 1.25rem;
}

.ui-strong,
.ui-bold {
  font-weight: 700;
}

.ui-demi {
  font-weight: 600;
}

a {
  color: #294A41;
  text-decoration: none;
  outline: none;
  transition: color 0.3s, border 0.3s;
}

b, strong {
  font-weight: 700;
}

img {
  max-width: 100%;
  height: auto;
}

hr {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
  margin: 0;
  margin-top: 20px;
  margin-bottom: 20px;
  padding: 0;
  height: 1px;
  border: 0;
  border-top: 1px solid #88A49C;
  overflow: hidden;
}

table {
  border-collapse: collapse;
}

.wrapper a img {
  border: none;
  display: block;
  line-height: 0;
  font-size: 0;
}

/*---- ICONS ---- */
::selection {
  background: #fff;
  color: #FFF;
}

::-moz-selection {
  background: #fff;
  color: #FFF;
}

@media (min-width: 992px) {
  ::-webkit-scrollbar {
    width: 8px;
    height: 8px;
    background-color: #88A49C;
  }

  ::-webkit-scrollbar-thumb {
    width: 8px;
    height: 8px;
    background-color: #1E3E35;
  }

  ::-webkit-scrollbar-thumb:hover {
    background: #386458;
  }
}
.ui-left {
  margin-left: auto;
}

.bg-primary {
  background-color: #294A41;
}

.bg-green-light {
  background-color: #E2F1EE;
}

.bg-purple {
  background-color: #884C9D;
}

.bg-pink {
  background-color: #FE5364;
}

.color-dark {
  color: #294A41;
}

.color-green-light {
  color: #88A49C;
}

.font-bold {
  font-weight: 600;
}

.font-semi {
  font-weight: 600;
}

.section-title {
  font-size: 24px;
  font-size: 1.5rem;
  font-weight: 700;
  line-height: normal;
  letter-spacing: 0.24px;
  letter-spacing: 0.015rem;
  text-transform: uppercase;
  margin-top: 0;
  margin-bottom: 40px;
}

.form {
  border-radius: 8px;
  background: #fff;
  box-shadow: 0px 4px 35px 0px rgba(114, 142, 174, 0.2);
  padding: 40px;
}
.form-image {
  position: relative;
  width: 116px;
  height: 116px;
  border-radius: 50%;
  background-color: #F5F9FF;
  display: block;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 20px;
}
.form-image img {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 80px;
  height: 80px;
}
.form-title {
  font-size: 20px;
  font-size: 1.25rem;
  font-weight: 600;
  text-transform: uppercase;
  text-align: center;
  margin-bottom: 40px;
}

.accordion {
  margin-bottom: 40px;
}
.accordion-head {
  padding-left: 20px;
  padding-right: 64px;
  color: #8D96A7;
  font-size: 14px;
  font-size: 0.875rem;
  font-weight: 600;
  margin-bottom: 10px;
}
.accordion-content {
  line-height: 1.2;
  height: 0;
  overflow: hidden;
  opacity: 0;
  visibility: hidden;
  transition: height 0.3s, transform 0.3s;
  transform: scale(0.2);
}
.accordion-content-items {
  border-bottom: 1px solid #E2F1EE;
  margin-bottom: 20px;
  padding-top: 20px;
}
.accordion-content-item {
  margin-bottom: 38px;
}
.accordion-content-item li {
  line-height: 1.2;
  position: relative;
  padding-left: 26px;
}
.accordion-content-item li:not(:last-of-type) {
  margin-bottom: 16px;
}
.accordion-content-item li::before {
  position: absolute;
  top: 8px;
  left: 0;
  content: "";
  width: 16px;
  height: 1px;
  background-color: #303030;
}
.accordion-trigger {
  position: relative;
  color: var(--Black, #303030);
  font-size: 18px;
  font-size: 1.125rem;
  font-weight: 600;
  display: flex;
  justify-content: space-between;
  gap: 20px;
  border-radius: 8px;
  background-color: #fff;
  cursor: pointer;
  padding: 28px 64px 28px 20px;
}
.accordion-trigger-text {
  flex-grow: 1;
}
.accordion-icon {
  position: absolute;
  right: 20px;
  top: 50%;
  transform: translateY(-50%);
  width: 24px;
  height: 24px;
  transition: transform 0.3s;
  flex-shrink: 0;
}
.accordion-item {
  border-radius: 8px;
  border: 1px solid #E2F1EE;
}
.accordion-item:not(:last-child) {
  margin-bottom: 10px;
}
.accordion-item.opened .accordion-trigger {
  background-color: #E2F1EE;
}
.accordion-item.opened .accordion-content {
  padding: 20px;
  height: initial;
  opacity: 1;
  visibility: visible;
  transform: scale(1);
}
.accordion-item.opened .accordion-content__simple {
  padding-bottom: 36px;
}
.accordion-item.opened .accordion-icon {
  transform: translateY(-50%) rotate(180deg);
}

.ui-tabs {
  white-space: nowrap;
  overflow-x: auto;
}

.text-uppercase {
  text-transform: uppercase;
}

.ui-btn {
  font-size: 15px;
  font-size: 0.9375rem;
  font-weight: 600;
  line-height: normal;
  height: 50px;
  display: inline-block;
  max-width: 100%;
  position: relative;
  padding-top: 14px;
  padding-bottom: 14px;
  padding-left: 44px;
  padding-right: 44px;
  border-radius: 4px;
  border: 1px solid #88A49C;
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
  background-color: #294A41;
  color: #fff;
  text-align: center;
  cursor: pointer;
  outline: none;
  transition: background-color 0.2s, transform 0.2s;
  display: flex;
  align-items: center;
  gap: 12px;
}
.ui-btn-icon {
  width: 20px;
  height: 20px;
  fill: none;
  display: inline-block;
  vertical-align: middle;
}
.ui-btn-block {
  display: block;
  width: 100%;
}
.ui-btn-small {
  font-size: 16px;
  font-size: 1rem;
  font-weight: 400;
  padding: 10px 36px;
  height: 40px;
}
.ui-btn-light {
  font-size: 14px;
  font-size: 0.875rem;
  color: #294A41;
  background-color: #E2F1EE;
  border-color: #E2F1EE;
  text-transform: uppercase;
  line-height: normal;
  padding-top: 12px;
  padding-bottom: 12px;
  padding-left: 30px;
  padding-right: 12px;
}
.ui-btn-light .ui-btn-icon {
  width: 22px;
  height: 22px;
}
.ui-btn-rounded {
  letter-spacing: 0.3px;
  letter-spacing: 0.01875rem;
  border-radius: 40px;
  padding-left: 48px;
  padding-right: 48px;
  padding-top: 20px;
  padding-bottom: 20px;
}
@media (max-width: 480px) {
  .ui-btn-rounded {
    display: flex;
    align-items: center;
    justify-content: center;
  }
}
.ui-btn:hover {
  transform: translateY(-1px);
}
.ui-btn-bg-red {
  background-color: #A60B15;
  border-color: #F58C8C;
}
.ui-btn-bg-blue {
  background-color: #005299;
  border-color: #328FDF;
}

.ui-field {
  display: flex;
  flex-direction: column;
  gap: 12px;
  position: relative;
  margin-bottom: 20px;
}
.ui-field label.error {
  font-size: 16px;
  font-size: 1rem;
  color: #FF0000;
  position: absolute;
  bottom: 0;
  left: 20px;
  z-index: 1;
}
@media (max-width: 599px) {
  .ui-field label.error {
    font-size: 12px;
    font-size: 0.75rem;
    left: 12px;
  }
}

.ui-label {
  font-size: 16px;
