  padding: 14px 28px;
  border-radius: 8px;
  border: 1px solid #88A49C;
  background-color: #EEF2F5;
  outline: none;
}
.ui-input.error {
  border-color: #FF0000;
}
.ui-input::placeholder {
  color: #A8B0BE;
}
.ui-input-file {
  display: none;
}

.ui-textarea__side {
  border-radius: 4px;
  border: 1px solid #E7EFFB;
  background-color: #fff;
  resize: none;
  padding: 16px;
  outline: none;
  height: 100px;
}
.ui-textarea__side::placeholder {
  color: #A8B0BE;
}

.jq-selectbox {
  display: block;
}

.ui-select.opened .jq-selectbox__trigger {
  transform: rotate(180deg);
}
.ui-select.error ~ .jq-selectbox__select {
  border-color: #FF0000;
}
.ui-select .jq-selectbox__select {
  color: #4F796E;
  padding: 14px 28px;
  border-radius: 8px;
  border: 1px solid #88A49C;
  background-color: #EEF2F5;
  outline: none;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.ui-select .jq-selectbox__select-text {
  line-height: normal;
}
.ui-select .jq-selectbox__dropdown {
  width: 100%;
  border-radius: 8px;
  padding: 30px 26px;
  background-color: #fff;
  margin-top: 10px;
  margin-bottom: 10px;
  box-shadow: 0px 4px 35px 0px rgba(114, 142, 174, 0.2);
}
.ui-select .jq-selectbox__dropdown li {
  cursor: pointer;
  transition: color 0.3s;
}
.ui-select .jq-selectbox__dropdown li:not(:last-child, .disabled) {
  margin-bottom: 28px;
}
.ui-select .jq-selectbox__dropdown li.disabled {
  height: 0;
  visibility: hidden;
  opacity: 0;
  overflow: hidden;
}
.ui-select .jq-selectbox__dropdown li:hover {
  color: #294A41;
}
.ui-select .jq-selectbox__trigger {
  background-image: url(../images/svg/select.svg);
