.header {
  background-color: #FCFCFC;
  display: flex;
  align-items: stretch;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  height: 100px;
  z-index: 11;
}
.header-logo {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #294A41;
  width: 280px;
  flex: 0 0 280px;
  padding-right: 20px;
  border: 1px solid #3A574F;
}
.header-logo-image {
  max-width: 180px;
  max-height: 54px;
}
.header-content {
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding-top: 24px;
  padding-bottom: 24px;
  flex-grow: 1;
  box-shadow: 0px 4px 35px 0px rgba(114, 142, 174, 0.2);
}
.header-content-wrapper {
  width: 100%;
  display: flex;
  align-items: center;
}
.header-info {
  display: flex;
  font-size: 14px;
  font-size: 0.875rem;
  margin-right: 22px;
}
.header-info-item {
  position: relative;
  padding-right: 18px;
}
.header-info-item:not(:first-child) {
  padding-left: 18px;
}
.header-info-item-title {
  display: inline-block;
  vertical-align: middle;
  margin-right: 6px;
}
.header-info-item-text {
  display: inline-block;
  vertical-align: middle;
}
.header-info-item::after {
  content: "";
  position: absolute;
  top: 50%;
  left: 100%;
  transform: translateY(-50%);
  width: 1px;
  height: 10px;
  background-color: #E7EFFB;
}
.header-info-item__weather .header-info-item-text {
  font-size: 13px;
  font-size: 0.8125rem;
}
.header-info-item__weather .header-info-item-text:first-of-type {
  margin-right: 8px;
}
.header-info-icon {
  fill: none;
  width: 20px;
  height: 20px;
  display: inline-block;
  vertical-align: middle;
  margin-right: 8px;
}
.header-currency {
  display: flex;
  flex-grow: 1;
  font-size: 14px;
  font-size: 0.875rem;
  margin-right: 22px;
}
.header-currency-item {
  padding-right: 8px;
}
.header-currency-item:not(:first-child) {
  padding-left: 18px;
}
.header-currency-item-title {
  display: inline-block;
  vertical-align: middle;
  margin-right: 6px;
}
.header-currency-item-text {
  font-size: 12px;
  font-size: 0.75rem;
  display: inline-block;
  vertical-align: middle;
  margin-left: 4px;
}
.header-currency-icon {
  fill: none;
  width: 24px;
  height: 24px;
  display: inline-block;
  vertical-align: middle;
  border-radius: 50%;
}
.header-user {
  position: relative;
  margin-left: 22px;
  background: #FFF;
  border-radius: 30px;
  box-shadow: 0px 4px 35px 0px rgba(114, 142, 174, 0.2);
  cursor: pointer;
  width: 120px;
}
.header-user-wrapper {
  padding: 6px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  position: absolute;
  top: -25px;
  right: 0;
  background-color: #FFF;
  border-radius: 30px;
  box-shadow: 0px 4px 35px 0px rgba(114, 142, 174, 0.2);
  transition: border-radius 0.3s;
}
.header-user-name {
  width: 0;
  height: 0;
  overflow: hidden;
  opacity: 0;
  visibility: hidden;
}
.header-user-label {
  width: 0;
  height: 0;
  overflow: hidden;
  opacity: 0;
  visibility: hidden;
  color: #8D96A7;
  font-size: 14px;
  font-size: 0.875rem;
}
.header-user-info {
  display: flex;
  align-items: center;
}
.header-user::before {
  content: "";
  position: absolute;
  top: 50%;
  right: 112px;
  transform: translateY(-50%);
  width: 1px;
  height: 32px;
  background-color: #E7EFFB;
}
.header-user-avatar {
  position: relative;
  font-size: 14px;
  font-size: 0.875rem;
  font-weight: 600;
  width: 38px;
  height: 38px;
  border-radius: 50%;
  background-color: #E2F1EE;
  margin-right: 38px;
}
.header-user-avatar-name {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.header-user-trigger {
  width: 24px;
  height: 24px;
  position: absolute;
  right: 12px;
  top: 12px;
  transition: transform 0.3s;
}
.header-user.opened .header-user-wrapper {
  padding: 20px 60px 20px 20px;
  border-radius: 8px;
}
.header-user.opened .header-user-avatar {
  margin-right: 10px;
}
.header-user.opened .header-user-label {
  width: max-content;
  height: max-content;
  overflow: visible;
  opacity: 1;
  visibility: visible;
  margin-bottom: 6px;
}
.header-user.opened .header-user-name {
  width: max-content;
  height: max-content;
  overflow: visible;
  opacity: 1;
  visibility: visible;
  font-weight: 600;
  min-width: 100px;
}
.header-user.opened .header-user-trigger {
  transform: rotate(180deg);
}

.wrapper {
  position: relative;
  min-height: 100%;
  min-width: 100%;
  display: flex;
  flex-direction: column;
  overflow: hidden;
}

.content-wrapper {
  display: -webkit-box;
  min-height: 100%;
  min-width: 100%;
  overflow: hidden;
  padding-top: 100px;
}

.non-scroll {
  overflow: hidden !important;
  height: 100vh;
}

.aside {
  width: 280px;
  flex: 0 0 280px;
}

.sidebar {
  background-color: #294A41;
  color: #fff;
  height: calc(100vh - 100px);
  position: fixed;
  top: 100px;
  left: 0;
  width: inherit;
  display: flex;
  flex-direction: column;
}

.nav {
  margin-top: 46px;
  margin-bottom: 42px;
  overflow-y: auto;
}
.nav::-webkit-scrollbar {
  width: 4px;
  height: 4px;
  background-color: #1E3E35;
}
.nav::-webkit-scrollbar-thumb {
  width: 4px;
  height: 4px;
  background-color: #88A49C;
}
.nav::-webkit-scrollbar-thumb:hover {
  background: #9fb6af;
}
.nav > ul > li {
  position: relative;
}
.nav > ul > li:has(> ul) > a::after {
  content: "";
  position: absolute;
  top: 50%;
  left: 206px;
  transform: translateY(-50%) rotate(180deg);
  width: 20px;
  height: 20px;
  background-image: url(../images/svg/angle-small-down.svg);
  background-repeat: no-repeat;
  background-size: 20px 20px;
  opacity: 0.6;
  transition: transform 0.3s;
}
.nav > ul > li.active:has(> ul) > a::after {
  transform: translateY(-50%) rotate(0deg);
  opacity: 1;
}
.nav > ul > li.active > a {
  background-color: #1E3E35;
}
.nav > ul > li.active > a::before {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  width: 8px;
  height: 100%;
  background-color: #88A49C;
}
.nav > ul > li.active .icon {
  fill: #fff;
}
.nav > ul > li.active ul {
  height: inherit;
  transition: height 0.3s, transform 0.3s;
  transform: scale(1);
  padding-top: 8px;
  padding-bottom: 8px;
}
.nav > ul > li a {
